import React, { useState, useEffect } from 'react';
import AddExpenseForm from './components/AddExpenseForm';
import AddIncomeForm from './components/AddIncomeForm';
import ExpenseList from './components/ExpenseList';
import ExpenseSummary from './components/ExpenseSummary';
import ExpenseTrends from './components/ExpenseTrends';
import localforage from 'localforage';

const App = () => {
  const [walletBalance, setWalletBalance] = useState(5000);
  const [expenses, setExpenses] = useState([]);

  useEffect(() => {
    localforage.getItem('walletBalance').then(balance => {
      if (balance) setWalletBalance(balance);
    });
    localforage.getItem('expenses').then(exp => {
      if (exp) setExpenses(exp);
    });
  }, []);

  useEffect(() => {
    localforage.setItem('walletBalance', walletBalance);
    localforage.setItem('expenses', expenses);
  }, [walletBalance, expenses]);

  const addExpense = (expense) => {
    if (expense.amount > walletBalance) {
      alert("Insufficient balance!");
      return;
    }
    setExpenses([...expenses, expense]);
    setWalletBalance(walletBalance - expense.amount);
  };

  const addIncome = (amount) => {
    setWalletBalance(walletBalance + amount);
  };

  const editExpense = (id, updatedExpense) => {
    const updatedExpenses = expenses.map(exp => exp.id === id ? updatedExpense : exp);
    setExpenses(updatedExpenses);
  };

  const deleteExpense = (id) => {
    const updatedExpenses = expenses.filter(exp => exp.id !== id);
    setExpenses(updatedExpenses);
  };

  return (
    <div className="app">
      <h1>Expense Tracker</h1>
      <div className="wallet">
        <h2>Wallet Balance: {walletBalance}</h2>
        <AddIncomeForm addIncome={addIncome} />
      </div>
      <AddExpenseForm addExpense={addExpense} />
      <ExpenseList expenses={expenses} editExpense={editExpense} deleteExpense={deleteExpense} />
      <ExpenseSummary expenses={expenses} />
      <ExpenseTrends expenses={expenses} />
    </div>
  );
};

export default App;
