import React from 'react';

const ExpenseList = ({ expenses, editExpense, deleteExpense }) => {
  return (
    <div>
      <h2>Expense List</h2>
      <ul>
        {expenses.map(exp => (
          <li key={exp.id}>
            {exp.title} - {exp.amount} - {exp.category} - {exp.date}
            <button onClick={() => editExpense(exp.id, exp)}>Edit</button>
            <button onClick={() => deleteExpense(exp.id)}>Delete</button>
          </li>
        ))}
      </ul>
    </div>
  );
};

export default ExpenseList;
